// Import dependencies
import React, { useState, useEffect } from 'react';
import { NavLink } from 'react-router-dom';

// Components
import Title from '../components/class/Title';
import GradientSphere from '../components/class/GradientSphere';
import BlurSphere from '../components/class/BlurSphere';
import GlanceCardManager from '../components/class/GlanceCardManager';
import ProjectCardManager from '../components/class/ProjectCardManager';

const HomePage = () => {

    const [gOrbs, setGOrbs] = useState([]);
    const [bOrbs, setBOrbs] = useState([]);

    // On mount
    
    useEffect(() => {
        // Create sphere particles
        const container = document.getElementById('--sphereWrapper');
        const width = container.offsetWidth;
        const height = container.offsetHeight;
        for (let x = 0; x < generateRandomNumber(3, 9); x++) {
            setGOrbs(prevState => [
                ...prevState,
                {index: x, cWidth: width, cHeight: height}
            ]);
        };
        for (let x = 0; x < generateRandomNumber(3, 9); x++) {
            setBOrbs(prevState => [
                ...prevState,
                {index: x, cWidth: width, cHeight: height}
            ]);
        };
    }, []);

    // Methods
    const generateRandomNumber = (min, max) => {
        const minInt = Math.ceil(min);
        const maxInt = Math.floor(max);
        return Math.floor(Math.random() * (maxInt - minInt + 1)) + minInt;
    };

    return (
        <React.Fragment>
            <div className="--gradientBackground">
                <div className="relative flex flex-col justify-center items-center h-full z-10 ml-16 md:ml-0">
                    <h1 id="title" className="text-slate-100 font-display font-bold text-4xl sm:text-5xl md:text-6xl lg:text-7xl pb-8 select-none">LIAM NIXON</h1>
                    <Title />
                </div>
                <ul id='--sphereWrapper' className="overflow-hidden absolute w-full h-full top-0 left-0">
                    {gOrbs.map((element) => {
                        return (
                            <GradientSphere key={'Gradient Sphere' + (Math.random() * 10)} containerHeight={element.cHeight} containerWidth={element.cWidth}/>
                        )
                    })}
                    {bOrbs.map((element) => {
                        return (
                            <BlurSphere key={'Blur Sphere' + (Math.random() * 10)} containerHeight={element.cHeight} containerWidth={element.cWidth}/>
                        )
                    })}
                </ul>
            </div>
            <div className="flex flex-col ml-16 md:ml-0 bg-neutral-50">
                <div className="h-auto w-full pt-8 md:pt-16 pb-4 md:pb-8">
                    <div
                    data-aos="fade-up"
                    data-aos-duration="500"
                    className="
                    flex flex-col relative
                    justify-center items-center text-center
                    h-auto top-1/4">
                        <h2
                        className="
                        mt-4
                        text-slate-700 w-1/2
                        font-display font-bold
                        text-xl sm:text-2xl md:text-3xl lg:text-4xl">
                            Developer, Designer and Freelancer
                        </h2>
                        <p
                        className="
                        text-slate-700
                        pt-8 w-1/2
                        font-body font-medium
                        text-sm sm:text-base md:text-xl lg:text-2xl">
                            My name is Liam Nixon, welcome to my website! I design and develop web content across multiple frameworks and using industry best-practice to provide clean, quality content that puts client needs first.
                        </p>
                    </div>
                </div>
                <div className="h-auto w-full py-4 md:py-8">
                    <GlanceCardManager/>
                </div>
                <div className="h-auto w-full py-4 md:py-8">
                    <div
                    data-aos="fade-up"
                    data-aos-duration="500"
                    className="
                    flex flex-col relative
                    justify-center items-center text-center
                    h-auto top-1/4">
                        <h2
                        className="
                        mt-4
                        text-slate-700 w-1/2
                        font-display font-bold
                        text-xl sm:text-2xl md:text-3xl lg:text-4xl">
                            Some of my work
                        </h2>
                        <p
                        className="
                        text-slate-700
                        pt-8 w-1/2
                        font-body font-medium
                        text-sm sm:text-base md:text-xl lg:text-2xl">
                            Below are some examples of my work, if you would like to see more, feel free to check out my
                            <NavLink to='/portfolio'>
                                <span className="--textColour"> portfolio </span>
                            </NavLink>
                            for further projects.
                        </p>
                    </div>
                </div>
                <div className="h-auto w-full py-4 md:py-8">
                    <ProjectCardManager/>
                </div>
                <div className="h-auto w-full pt-4 md:pt-8 pb-8 md:pb-16">
                    <div
                    data-aos="fade-up"
                    data-aos-duration="500"
                    className="
                    flex flex-col relative
                    justify-center items-center text-center
                    h-auto top-1/4">
                        <p 
                        className='
                        mt-4
                        text-slate-700 w-2/3
                        font-display
                        text-lg md:text-xl'>
                            <em>I acknowledge the Traditional Owners of the land on which I work and live. I pay my respects to their Elders past, present and emerging. I celebrate the stories, culture and traditions of Aboriginal and Torres Strait Islander Elders of all communities who also work and live on this land.</em>
                        </p>
                    </div>
                </div>
            </div>
        </React.Fragment>
    );
};

export default HomePage;