// Import dependencies
import React from 'react';
import ReactDOM from 'react-dom/client';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import AOS from 'aos';

// CSS
import "aos/dist/aos.css";

// Components
import Navbar from './components/functions/Navbar';
import Footer from './components/functions/Footer';

// Pages
import HomePage from './pages/HomePage';
import PortfolioPage from './pages/PortfolioPage';
import ContactMePage from './pages/ContactMePage';
import ErrorPage from './pages/ErrorPage';

// Portfolio pieces
import PerformLivestream from './pages/portfolio/PerformLivestream';
import BantamWest from './pages/portfolio/BantamWest';
import AuthenticationApp from './pages/portfolio/AuthenticationApp';
import RMGDashboard from './pages/portfolio/RMGDashboard';

// Stylesheet
import './css/Tailwind.css';
import './css/GlobalStyles.css';
import './css/Backgrounds.css';
import './css/TextStyles.css';

const root = ReactDOM.createRoot(document.getElementById('root'));

AOS.init({
    once: true
});
        document.addEventListener("DOMContentLoaded", function() {
            setTimeout(function() { AOS.refresh(); }, 500);
        });

root.render (
    <React.Fragment>
        <Router>
            <div className='flex bg-slate-700'>
                <Navbar />
                <div className='overflow-hidden w-full'>
                    <Routes>
                        <Route path='/' element={<HomePage/>}/>
                        <Route path='/portfolio' element={<PortfolioPage/>}/>
                        <Route path='/portfolio/rmg-dashboard' element={<RMGDashboard/>}/>
                        <Route path='/portfolio/perform-livestream' element={<PerformLivestream/>}/>
                        <Route path='/portfolio/bantam-west' element={<BantamWest/>}/>
                        <Route path='/portfolio/authentication-app' element={<AuthenticationApp/>}/>
                        <Route path='/contact' element={<ContactMePage/>}/>
                        <Route path='*' element={<ErrorPage/>}/>
                    </Routes>
                    <footer>
                        <Footer />
                    </footer>
                </div>
            </div>
        </Router>
    </React.Fragment>
);