// Import dependencies
import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';

// Components
import TooltipIcon from '../../components/functions/TooltipIcon';

// Images
import IllustratorIcon from '../../assets/icons/adobe-illustrator-icon.svg';
import PhotoshopIcon from '../../assets/icons/adobe-photoshop-icon.svg';

const PerformLivestream = () => {

    // Initialize data state
    const initState = () => {
        return {
            role: 'Freelancer/Designer',
            date: '2021',
            description: 'Bantam West is a board game created by Ike Brunicardi. I had the privelage of designing the world map asset used for the game while I was operating my freelancing business, Naturally Chaotic.'
        };
    };

    // State
    const [data] = useState(initState());
    const [toggle, setToggle] = useState(false);

    // Methods
    const isHovering = () => {
        setToggle(true);
      };
    
      const notHovering = () => {
        setToggle(false);
      };

    // Dynamic styling
    const showBar = {
        backgroundColor: 'rgba(30, 41, 59, 0.75)',
        bottom: 0
      };
      const hideBar = {
        backgroundColor: 'rgba(30, 41, 59, 0)',
        bottom: '-12.5%'
      };

      const showText = {
        color: 'rgba(241, 245, 249, 1)'
      };

      const hideText = {
        color: 'rgba(241, 245, 249, 0)'
      };

    return (
        <div className='flex flex-col items-center w-3/5 mx-auto mt-24'>
            <nav className='flex flex-row w-auto gap-0.5 sm:gap-1 lg:gap-2 self-start'>
                <NavLink to='/portfolio' className='font-body font-medium text-slate-100 text-xs sm:text-sm md:text-base lg:text-lg hover:underline'>Portfolio</NavLink>
                <span className='material-icons text-slate-100 text-xs sm:text-sm md:text-lg lg:text-xl select-none'>keyboard_arrow_right</span>
                <NavLink to='/portfolio/BantamWest' className='font-body font-medium text-slate-100 text-xs sm:text-sm md:text-base lg:text-lg hover:underline'>Bantam West</NavLink>
            </nav>
            <h1 className='text-slate-100 font-display font-bold text-2xl sm:text-3xl md:text-4xl
            self-start mb-4'>Bantam West</h1>
            <div onMouseOver={isHovering} onMouseOut={notHovering} className='w-full overflow-hidden relative'>
                <img src={require('../../assets/portfolio-bantam-west-splash.png')} alt="Bantam West Splash" className='w-full'></img>
                <div style={ toggle ? showBar : hideBar } className='absolute w-full lg:h-1/5 xl:h-[12.5%] transition-all duration-150'>
                    <p style={ toggle ? showText : hideText } className='w-full h-full p-2 font-body font-medium text-xs md:text-sm lg:text-base transition-all duration-150'>{ data.description }</p>
                </div>
            </div>
            <hr className='w-full mt-2 sm:mt-4 md:mt-6 lg:mt-8' />
            <div className='flex flex-row justify-between w-full'>
                <div className='flex flex-row justify-start w-auto gap-4 sm:gap-8 md:gap-12 lg:gap-16'>
                    <div className='w-auto self-center'>
                        <h3
                        className='font-body font-medium text-slate-100
                        text-sm sm:text-base md:text-xl lg:text-2xl'>
                            Role:
                        </h3>
                        <h4
                        className='font-body text-slate-100
                        text-xs sm:text-sm md:text-lg lg:text-xl'>
                            { data.role }
                        </h4>
                    </div>
                    <div className='w-auto self-center'>
                        <h3
                        className='font-body font-medium text-slate-100
                        text-sm sm:text-base md:text-xl lg:text-2xl'>
                            Date:
                        </h3>
                        <h4
                        className='font-body text-slate-100
                        text-xs sm:text-sm md:text-lg lg:text-xl'>
                            { data.date }
                        </h4>
                    </div>
                </div>
                <div className='flex flex-row w-auto gap-x-2 justify-end my-4'>
                    <h3
                    className='font-body font-medium self-center text-slate-100
                    text-sm sm:text-base md:text-xl lg:text-2xl'>
                        Tools Used:
                    </h3>
                    <TooltipIcon 
                        tooltip='Photoshop'
                        imageURL={ PhotoshopIcon }
                        altText='Photoshop Icon'
                    />
                    <TooltipIcon 
                        tooltip='Illustrator'
                        imageURL={ IllustratorIcon }
                        altText='Illustrator Icon'
                    />
                </div>
            </div>
            <hr className='w-full mb-2 sm:mb-4 md:mb-6 lg:mb-8' />
            <div className='flex gap-x-4'>
                <h2 className='text-slate-100 font-display font-bold text-4xl sm:text-5xl md:text-6xl'>"</h2>
                <p className='font-body text-slate-100 text-xl sm:text-2xl md:text-3xl inline-block self-center uppercase'><em>Our whole team is blown away by their work</em></p>
                <h2 className='text-slate-100 font-display font-bold text-4xl sm:text-5xl md:text-6xl'>"</h2>
            </div>
            <h1 className='text-slate-100 font-display font-bold text-2xl sm:text-3xl md:text-4xl self-start mb-4'>Project Aim</h1>
            <p
            className='font-body text-slate-100
            text-xs sm:text-sm md:text-lg lg:text-xl self-start mb-2 sm:mb-4 md:mb-6 lg:mb-8'>
                Bantam West is a board game by Ike Brunicardi, which I was contracted as a designer to assist with for their Kickstarter. 
                Ike needed assets produced for their project, such as a game map, some hand-drawn elements and a set of icons for their game.

            </p>
            <h1 className='text-slate-100 font-display font-bold text-2xl sm:text-3xl md:text-4xl self-start mb-4'>Challenges</h1>
            <p
            className='font-body text-slate-100
            text-xs sm:text-sm md:text-lg lg:text-xl self-start mb-2 sm:mb-4 md:mb-6 lg:mb-8'>
                While I have experience with digital design, I had not done very much with hand-drawn assets. This was new territory for me at the 
                time, but it's not in my personality to shy away from a challenge.
            </p>
            <h1 className='text-slate-100 font-display font-bold text-2xl sm:text-3xl md:text-4xl self-start mb-4'>The Process</h1>
            <p
            className='font-body text-slate-100
            text-xs sm:text-sm md:text-lg lg:text-xl self-start mb-2 sm:mb-4 md:mb-6 lg:mb-8'>
                The bulk of the work that I did for this project was achieved through Adobe Photoshop, with which I created large, detailed design files 
                based on my client's specifications and requirements. I did most of the work via graphic tablet, though a lot of post processing was handled 
                on a desktop computer.
                <br/><br/>
                Photoshop being a raster graphics engine, however, was not a suitable option for an icon set. As such, I produced a series of icons through 
                Adobe Illustrator, which were in a vector (.svg) format, which could be scaled to any required resolution needed by the client.
            </p>
            <h1 className='text-slate-100 font-display font-bold text-2xl sm:text-3xl md:text-4xl self-start mb-4'>Outcome</h1>
            <p
            className='font-body text-slate-100
            text-xs sm:text-sm md:text-lg lg:text-xl self-start mb-2 sm:mb-4 md:mb-6 lg:mb-8'>
                Bantam West reached a total of 625% funding by the end of the Kickstarter Campaign and successfully launched, my client was ecstatic with the outcome. 
                I have since worked alongside Ike on a few smaller works, but I am first and foremost a web developer, so I have not pursued any large-scale projects like 
                this one again since.
            </p>
        </div>
    );
};

export default PerformLivestream;