// Import dependencies
import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';

// Components
import TooltipIcon from '../../components/functions/TooltipIcon';

// Images
import ReactJSIcon from '../../assets/icons/react-js-icon.svg';
import TailwindIcon from '../../assets/icons/tailwind-css-icon.svg';
import MongoDBIcon from '../../assets/icons/mongodb-icon.svg';
import ExpressIcon from '../../assets/icons/express-js-icon.svg';
import NodeJSIcon from '../../assets/icons/node-js-icon.svg';

const AuthenticationApp = () => {

    // Initialize data state
    const initState = () => {
        return {
            role: 'Junior Developer',
            date: '2022',
            description: 'Perform Livestream is a streaming service owned and operated by Echelon Productions and is used to deliver learning content to schools around Australia.'
        };
    };

    // State
    const [data] = useState(initState());
    const [toggle, setToggle] = useState(false);

    // Methods
    const isHovering = () => {
        setToggle(true);
      };
    
      const notHovering = () => {
        setToggle(false);
      };

    // Dynamic styling
    const showBar = {
        backgroundColor: 'rgba(30, 41, 59, 0.75)',
        bottom: 0
      };
      const hideBar = {
        backgroundColor: 'rgba(30, 41, 59, 0)',
        bottom: '-12.5%'
      };

      const showText = {
        color: 'rgba(241, 245, 249, 1)'
      };

      const hideText = {
        color: 'rgba(241, 245, 249, 0)'
      };

    return (
        <div className='flex flex-col items-center w-3/5 mx-auto mt-24'>
            <nav className='flex flex-row w-auto gap-0.5 sm:gap-1 lg:gap-2 self-start'>
                <NavLink to='/portfolio' className='font-body font-medium text-slate-100 text-xs sm:text-sm md:text-base lg:text-lg hover:underline'>Portfolio</NavLink>
                <span className='material-icons text-slate-100 text-xs sm:text-sm md:text-lg lg:text-xl select-none'>keyboard_arrow_right</span>
                <NavLink to='/portfolio/perform-livestream' className='font-body font-medium text-slate-100 text-xs sm:text-sm md:text-base lg:text-lg hover:underline'>Authentication App</NavLink>
            </nav>
            <h1 className='text-slate-100 font-display font-bold text-2xl sm:text-3xl md:text-4xl
            self-start mb-4'>Perform Livestream</h1>
            <div onMouseOver={isHovering} onMouseOut={notHovering} className='w-full overflow-hidden relative'>
                <img src={require('../../assets/portfolio-authentication-splash.png')} alt="Authentication App Splash" className='w-full'></img>
                <div style={ toggle ? showBar : hideBar } className='absolute w-full lg:h-1/5 xl:h-[12.5%] transition-all duration-150'>
                    <p style={ toggle ? showText : hideText } className='w-full h-full p-2 font-body font-medium text-xs md:text-sm lg:text-base transition-all duration-150'>{ data.description }</p>
                </div>
            </div>
            <hr className='w-full mt-2 sm:mt-4 md:mt-6 lg:mt-8' />
            <div className='flex flex-row justify-between w-full'>
                <div className='flex flex-row justify-start w-auto gap-4 sm:gap-8 md:gap-12 lg:gap-16'>
                    <div className='w-auto self-center'>
                        <h3
                        className='font-body font-medium text-slate-100
                        text-sm sm:text-base md:text-xl lg:text-2xl'>
                            Role:
                        </h3>
                        <h4
                        className='font-body text-slate-100
                        text-xs sm:text-sm md:text-lg lg:text-xl'>
                            { data.role }
                        </h4>
                    </div>
                    <div className='w-auto self-center'>
                        <h3
                        className='font-body font-medium text-slate-100
                        text-sm sm:text-base md:text-xl lg:text-2xl'>
                            Date:
                        </h3>
                        <h4
                        className='font-body text-slate-100
                        text-xs sm:text-sm md:text-lg lg:text-xl'>
                            { data.date }
                        </h4>
                    </div>
                </div>
                <div className='flex flex-row w-auto gap-x-2 justify-end my-4'>
                    <h3
                    className='font-body font-medium self-center text-slate-100
                    text-sm sm:text-base md:text-xl lg:text-2xl'>
                        Tools Used:
                    </h3>
                    <TooltipIcon 
                        tooltip='React'
                        imageURL={ ReactJSIcon }
                        altText='React Icon'
                    />
                    <TooltipIcon 
                        tooltip='MongoDB'
                        imageURL={ MongoDBIcon }
                        altText='MongoDB Icon'
                    />
                    <TooltipIcon 
                        tooltip='Express'
                        imageURL={ ExpressIcon }
                        altText='Express Icon'
                    />
                    <TooltipIcon 
                        tooltip='Node'
                        imageURL={ NodeJSIcon }
                        altText='Node Icon'
                    />
                    <TooltipIcon 
                        tooltip='Tailwind CSS'
                        imageURL={ TailwindIcon }
                        altText='Tailwind Icon'
                    />
                </div>
            </div>
            <hr className='w-full mb-2 sm:mb-4 md:mb-6 lg:mb-8' />
            <div className='flex gap-x-4'>
                <h2 className='text-slate-100 font-display font-bold text-4xl sm:text-5xl md:text-6xl'>"</h2>
                <p className='font-body text-slate-100 text-xl sm:text-2xl md:text-3xl inline-block self-center uppercase'><em>A MERN stack test</em></p>
                <h2 className='text-slate-100 font-display font-bold text-4xl sm:text-5xl md:text-6xl'>"</h2>
            </div>
            <h1 className='text-slate-100 font-display font-bold text-2xl sm:text-3xl md:text-4xl self-start mb-4'>Project Aim</h1>
            <p
            className='font-body text-slate-100
            text-xs sm:text-sm md:text-lg lg:text-xl self-start mb-2 sm:mb-4 md:mb-6 lg:mb-8'>
                This was a personal project of mine. As a front-end developer I typically focus on technologies such as React, HTML, CSS, etc. 
                But as an individual with an active growth mindset, I am always looking to expand my skillset and knowledge. As such, I spend 
                much of my own time researching new technologies and expanding into unknown territory. One of these personal pursuits is to 
                begin learning a back-end technology stack.

            </p>
            <h1 className='text-slate-100 font-display font-bold text-2xl sm:text-3xl md:text-4xl self-start mb-4'>Challenges</h1>
            <p
            className='font-body text-slate-100
            text-xs sm:text-sm md:text-lg lg:text-xl self-start mb-2 sm:mb-4 md:mb-6 lg:mb-8'>
                Due to having little-to-no experience at the time, this project was surprisingly intimidating, as it required learning 
                a plethora of new skills and technologies. For example, building a foundational understanding of HTTP req/res functionality, 
                the manner in which a NoSQL database such as MongoDB works (I have educational experience with standard SQL) and the testing of 
                such technologies.
                <br/><br/>
                Another element which I wanted to pursue on this project was to explore data security and encryption, as user login interfaces need 
                to be secure to ensure the safety of the users. This involved further research into how hashes are used in cybersecurity and how to 
                implement them within a project.
            </p>
            <h1 className='text-slate-100 font-display font-bold text-2xl sm:text-3xl md:text-4xl self-start mb-4'>The Process</h1>
            <p
            className='font-body text-slate-100
            text-xs sm:text-sm md:text-lg lg:text-xl self-start mb-2 sm:mb-4 md:mb-6 lg:mb-8'>
                To begin with, I set up a MongoDB Atlas account to create a simple NoSQL database, with which I could store and retrieve account data 
                to handle dummy user profiles. I also created a lightweight server using Node to handle HTTP requests and API endpoints, which I tested 
                using Postman Agent.
                <br/><br/>
                In addition to setting up the general functionality and database, I also used a npm package to handle encrypting user information using 
                salted hashes, which were stored in the database, to ensure that information was properly secured. Once this was added, it was simply a 
                matter of adding input validation and a handler for incorrect user information to test whether to encryption/decryption worked between the 
                client and server.
            </p>
            <h1 className='text-slate-100 font-display font-bold text-2xl sm:text-3xl md:text-4xl self-start mb-4'>Outcome</h1>
            <p
            className='font-body text-slate-100
            text-xs sm:text-sm md:text-lg lg:text-xl self-start mb-2 sm:mb-4 md:mb-6 lg:mb-8'>
                By the end of this experiment I had a functional user authentication system which supported logging in and creating new user accounts which 
                were stored and managed in a live NoSQL database. While there is nothing on the other side of the login page, I simply intended to learn how 
                to begin implementing back-end technologies with the front-end skillset that I am familiar with.
            </p>
        </div>
    );
};

export default AuthenticationApp;