// Import dependencies
import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';

// Components
import TooltipIcon from '../../components/functions/TooltipIcon';

// Images
import VueIcon from '../../assets/icons/vue-js-icon.svg';
import TailwindIcon from '../../assets/icons/tailwind-css-icon.svg';

const PerformLivestream = () => {

    // Initialize data state
    const initState = () => {
        return {
            role: 'Junior Developer',
            date: '2022',
            description: 'Perform Livestream is a streaming service owned and operated by Echelon Productions and is used to deliver learning content to schools around Australia.'
        };
    };

    // State
    const [data] = useState(initState());
    const [toggle, setToggle] = useState(false);

    // Methods
    const isHovering = () => {
        setToggle(true);
      };
    
      const notHovering = () => {
        setToggle(false);
      };

    // Dynamic styling
    const showBar = {
        backgroundColor: 'rgba(30, 41, 59, 0.75)',
        bottom: 0
      };
      const hideBar = {
        backgroundColor: 'rgba(30, 41, 59, 0)',
        bottom: '-12.5%'
      };

      const showText = {
        color: 'rgba(241, 245, 249, 1)'
      };

      const hideText = {
        color: 'rgba(241, 245, 249, 0)'
      };

    return (
        <div className='flex flex-col items-center w-3/5 mx-auto mt-24'>
            <nav className='flex flex-row w-auto gap-0.5 sm:gap-1 lg:gap-2 self-start'>
                <NavLink to='/portfolio' className='font-body font-medium text-slate-100 text-xs sm:text-sm md:text-base lg:text-lg hover:underline'>Portfolio</NavLink>
                <span className='material-icons text-slate-100 text-xs sm:text-sm md:text-lg lg:text-xl select-none'>keyboard_arrow_right</span>
                <NavLink to='/portfolio/perform-livestream' className='font-body font-medium text-slate-100 text-xs sm:text-sm md:text-base lg:text-lg hover:underline'>Perform Livestream</NavLink>
            </nav>
            <h1 className='text-slate-100 font-display font-bold text-2xl sm:text-3xl md:text-4xl
            self-start mb-4'>Perform Livestream</h1>
            <div onMouseOver={isHovering} onMouseOut={notHovering} className='w-full overflow-hidden relative'>
                <img src={require('../../assets/portfolio-perform-livestream-splash.png')} alt="Perform Livestream Splash" className='w-full'></img>
                <div style={ toggle ? showBar : hideBar } className='absolute w-full lg:h-1/5 xl:h-[12.5%] transition-all duration-150'>
                    <p style={ toggle ? showText : hideText } className='w-full h-full p-2 font-body font-medium text-xs md:text-sm lg:text-base transition-all duration-150'>{ data.description }</p>
                </div>
            </div>
            <hr className='w-full mt-2 sm:mt-4 md:mt-6 lg:mt-8' />
            <div className='flex flex-row justify-between w-full'>
                <div className='flex flex-row justify-start w-auto gap-4 sm:gap-8 md:gap-12 lg:gap-16'>
                    <div className='w-auto self-center'>
                        <h3
                        className='font-body font-medium text-slate-100
                        text-sm sm:text-base md:text-xl lg:text-2xl'>
                            Role:
                        </h3>
                        <h4
                        className='font-body text-slate-100
                        text-xs sm:text-sm md:text-lg lg:text-xl'>
                            { data.role }
                        </h4>
                    </div>
                    <div className='w-auto self-center'>
                        <h3
                        className='font-body font-medium text-slate-100
                        text-sm sm:text-base md:text-xl lg:text-2xl'>
                            Date:
                        </h3>
                        <h4
                        className='font-body text-slate-100
                        text-xs sm:text-sm md:text-lg lg:text-xl'>
                            { data.date }
                        </h4>
                    </div>
                </div>
                <div className='flex flex-row w-auto gap-x-2 justify-end my-4'>
                    <h3
                    className='font-body font-medium self-center text-slate-100
                    text-sm sm:text-base md:text-xl lg:text-2xl'>
                        Tools Used:
                    </h3>
                    <TooltipIcon 
                        tooltip='Vue.js'
                        imageURL={ VueIcon }
                        altText='Vue.js Icon'
                    />
                    <TooltipIcon 
                        tooltip='Tailwind CSS'
                        imageURL={ TailwindIcon }
                        altText='Tailwind Icon'
                    />
                </div>
            </div>
            <hr className='w-full mb-2 sm:mb-4 md:mb-6 lg:mb-8' />
            <div className='flex gap-x-4'>
                <h2 className='text-slate-100 font-display font-bold text-4xl sm:text-5xl md:text-6xl'>"</h2>
                <p className='font-body text-slate-100 text-xl sm:text-2xl md:text-3xl inline-block self-center uppercase'><em>Local education, national delivery</em></p>
                <h2 className='text-slate-100 font-display font-bold text-4xl sm:text-5xl md:text-6xl'>"</h2>
            </div>
            <h1 className='text-slate-100 font-display font-bold text-2xl sm:text-3xl md:text-4xl self-start mb-4'>Project Aim</h1>
            <p
            className='font-body text-slate-100
            text-xs sm:text-sm md:text-lg lg:text-xl self-start mb-2 sm:mb-4 md:mb-6 lg:mb-8'>
                Perform Livestream was the answer to a nation-wide crisis, the COVID-19 lockdowns that affected billions around the world. 
                The client, Echelon Productions, operated by creating and delivering educational theatre performances via in-school visits 
                and programs. However, when the pandemic hit, Echelon Productions, as with many companies across the country had to adapt 
                quickly to unpredictable and often volatile conditions. Perform Livestreams is a project that aims to deliver the same, 
                quality content to students around Australia and New Zealand through a livestream format.

            </p>
            <h1 className='text-slate-100 font-display font-bold text-2xl sm:text-3xl md:text-4xl self-start mb-4'>Challenges</h1>
            <p
            className='font-body text-slate-100
            text-xs sm:text-sm md:text-lg lg:text-xl self-start mb-2 sm:mb-4 md:mb-6 lg:mb-8'>
                As a junior developer on this project, there were several challenges that I faced while working on this project. 
                Firstly, was the hurdle of jumping into an ongoing project that was headed by the company's senior developer and 
                understanding their aims and processes.
                <br/> <br/>
                This was one of my first, major introductions to the Vue framework and the learning curve was steep. Having worked 
                primarily through native JavaScript and some JQuery in the past, there was a lot to learn.
                <br/> <br/>
                Additionally, this project was intended to run for a live, paying audience. Meaning that proper debugging, iterative 
                development and frequent communication was an absolute must.
            </p>
            <h1 className='text-slate-100 font-display font-bold text-2xl sm:text-3xl md:text-4xl self-start mb-4'>The Process</h1>
            <p
            className='font-body text-slate-100
            text-xs sm:text-sm md:text-lg lg:text-xl self-start mb-2 sm:mb-4 md:mb-6 lg:mb-8'>
                My largest contribution to this platform, as shown in the splash image above, was the introduction of a new landing 
                page that delivered previous livestreams at various education levels to paying clients. One of the primary concerns 
                that I had, was ensuring that the videos couldn't be accessed easily by those who had not paid for the program. For 
                example, a user that manages to extrapolate the website URL to switch shows without a direct link.
                <br/> <br/>
                The solution? Creating a randomized token that is passed to the navigational links and stored locally in the session 
                storage on the client's system, which is then verified on the program page. If the token passed in the URL does not 
                match the token stored in the client session storage, then they are immediately returned to the login page of the 
                platform. While this is not as sophisticated as a full-blown authentication system that integrates back-end processes 
                and databases, it is a lightweight and effective solution to the problem.
            </p>
            <h1 className='text-slate-100 font-display font-bold text-2xl sm:text-3xl md:text-4xl self-start mb-4'>Outcome</h1>
            <p
            className='font-body text-slate-100
            text-xs sm:text-sm md:text-lg lg:text-xl self-start mb-2 sm:mb-4 md:mb-6 lg:mb-8'>
                Ultimately, the project is still ongoing, but my contributions as discussed above have all proven successful in 
                achieving their intended purpose and have met the KPIs set forth by the client. Primarily ensuring that paying 
                users can access the content safely, quickly and reliably, as well as having a lightweight security system in place.
            </p>
        </div>
    );
};

export default PerformLivestream;